/* This fires as soon as the element enters the dorm */
.list-transition-enter,
.list-transition-appear {
  /*We give the list the initial dimension of the list button*/
   width: 15rem;
   transition: all 900ms;

  max-height: 5rem;
  bottom: 5.5rem;
  left: 5rem;
  color: transparent;
  transform:   scale(1) 
  translateY(0)
}
/* This is where we can add the transition*/
.list-transition-enter-active,
.list-transition-appear-active {
  width:65rem;
  max-height:  40rem;
  transition: all 900ms;
  border-radius: 55px;
  bottom: 5.5rem;
  left: 5rem;
  transform: scale(1) ;

}

/* This fires as soon as the this.state.showList is false */
.list-transition-exit {
  width:65rem;
  bottom: 5.5rem;
  left: 3rem;
  transform:   scale(1) ;
  max-height: 40rem;
  transition: all 900ms;
}
/* fires as element leaves the DOM*/
.list-transition-exit-active {
  width: 15rem;
  bottom: 5.5rem; 
   max-height: 5rem;
   transform:   scale(1) translateY(0);
  color: transparent;
  transition: all 900ms;
  border-radius: 55px;
}

